import request from '@/utils/request';

//
export function getUserList(queryData){
    return request({
        url: 'api/userinfo/list',
        method: 'get',
        params: queryData
    })
}

//
export function addUser(user){
    return request({
        url: 'api/userinfo/addUser',
        method: 'post',
        data: user
    })
}

