<template>
  <!--  -->
  <div>
    <el-dialog title="" :visible="visible" :before-close="handleClose">
      <el-form :model="form" class="demo-form-inline" label-width="90px" :rules="rules">
        <el-row :gutter="gutter">
          <el-col :span="11">
            <el-form-item label="">
              <el-input v-model="form.nickname"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="11">
            <el-form-item label="">
              <el-select v-model="form.isactive" placeholder="">
                <el-option label="" value="1"></el-option>
                <el-option label="" value="0"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="gutter">
          <el-col :span="11">
            <el-form-item label="" prop="username">
              <el-input v-model="form.username"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="11">
            <el-form-item label="">
              <el-input v-model="form.password"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="gutter">
          <el-col :span="11">
            <el-form-item label="">
              <el-input v-model="form.phone"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="11">
            <el-form-item label="">
              <el-input v-model="form.email"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="gutter">          
          <el-col :span="11">
            <el-form-item label="">
              <el-date-picker v-model="form.expirydate" type="date" placeholder="" format="yyyy-MM-dd"></el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleClose"> </el-button>
        <el-button type="primary" @click="handleSave"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { addUser } from "@/api/usermanage";
export default {
  name: "",
  components: {},
  props: {
    visible: {
      type: Boolean,
      required: true
    }
  },
  data() {
    return {
      form: {
        username: "",
        password: "",
        nickname: "",
        profile: "",
        phone: "",
        email: "",
        age: "",
        isactive: "1",
        expirydate: ""
      },
      gutter: 100, //
      rules: {
        //
        username: [{ required: true, message: "", trigger: "blur" }]
      }
    };
  },
  watch: {},
  computed: {},
  methods: {
    handleSave() {
      //
      addUser(this.form)
        .then(response => {
          if (response.code == 200) {
            this.$message({
              message: "",
              type: "success"
            });            
          } else {
            this.$message({
              message: "",
              type: "error"
            });
            console.log(response.message);
          }
          this.$emit("close", {close:false,query: true});
        })
    },
    handleClose(done) {
      //
      this.$confirm("").then(_ => {
          done;          
        }).catch(_ => {
          console.log(_);
        }).finally(_ => {
          this.$emit("close", {close:false,query: false});
        })
    }
  }
};
</script>
<style lang="scss" scoped>
</style>