<template>
  <!--  -->
  <div class="padding-container">
    <!--  -->
    <el-form :inline="true" :model="queryData" class="demo-form-inline">
      <el-form-item label="">
        <el-input v-model="queryData.queryData.nickName" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="queryData.queryData.isactive" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value="1"></el-option>
          <el-option label="" value="0"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
          <el-input v-model="queryData.queryData.username" placeholder=""></el-input>
        </el-form-item>
      <div class="right">        
        <el-form-item>
          <el-button type="primary" @click="query"></el-button>
        </el-form-item>
      </div>
    </el-form>

    <el-row>
      <el-button type="primary" @click="dialogAddVisible = true"></el-button>
      <!-- <el-button type="success"></el-button>
      <el-button type="info"></el-button>
      <el-button type="warning"></el-button>
      <el-button type="danger"></el-button>-->
    </el-row>
    <!--  -->
    <el-table
      ref="multipleTable"
      highlight-current-row
      stripe
      border
      :data="tableData"
      tooltip-effect="dark"
      style="width: 100%"
      v-loading="loading"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="50" align="center"></el-table-column>
      <el-table-column prop="nickname" label="" show-overflow-tooltip align="center"></el-table-column>
      <el-table-column prop="email" label="" show-overflow-tooltip align="center"></el-table-column>
      <el-table-column prop="phone" label="" show-overflow-tooltip align="center"></el-table-column>
      <el-table-column prop="username" label="" show-overflow-tooltip align="center"></el-table-column>
      <el-table-column prop="isactive" label="" show-overflow-tooltip align="center">
        <template slot-scope="scope">
          <el-tag
            :type="scope.row.isactive == 1 ? 'success' : 'danger'"
            disable-transitions
          >{{scope.row.isactive==1 ? '' : ''}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="expirydate" label="" show-overflow-tooltip align="center">
        <template slot-scope="scope">
          <span :style="{color: isExpire(scope.row.expirydate) ? 'red' : ''}">
    
          {{scope.row.expirydate ? scope.row.expirydate.substr(0,10) : ""}}
          </span>
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="" width="400" align="center">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="success" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button
            size="mini"
            type="danger"
            @click.native.prevent="deleteRow(scope.$index, tableData)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <div style="margin-top: 20px">
      <el-button @click="toggleSelection([tableData[1], tableData[2]])"></el-button>
      <el-button @click="toggleSelection()"></el-button>
    </div>

    <!--  -->
    <div class="block">
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pageNation.pageNum"
        :page-sizes="pageNation.pageSizes"
        :page-size="pageNation.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="pageNation.total"
      ></el-pagination>
    </div>

    <!-- - -->
    <add :visible="dialogAddVisible" @close="dialogAdd" />
  </div>
</template>

<script>
import { getUserList } from "@/api/usermanage";
import add from "./add";
import tools from "@/utils/tools";

export default {
  name: "Organization",
  components: { add },
  props: [],
  data() {
    return {
      loading: true,  //
      dialogAddVisible: false, //
      queryData: {  //        
        queryData: {
          username: null,
          isactive: null,
          expirydate: null
        }
      },
      pageNation: { //
        pageNum: 1,
        pageSize: 10,
        pageSizes: [10, 20, 30, 40, 50],
        total: 0
      },
      tableData: null,  //
      multipleSelection: [] //
    };
  },
  watch: {},
  computed: {},
  methods: {
    query() {//    
      this.loading = true;
      this.queryData.pageNum = this.pageNation.pageNum
      this.queryData.pageSize = this.pageNation.pageSize
      getUserList(this.queryData).then(response => {
        let data = response.data
        this.tableData = data.list;
        this.pageNation.pageNum = data.pageNum;
        this.pageNation.pageSize = data.pageSize;
        this.pageNation.total = data.total;       
      }).finally(_ => this.loading = false);
    },
    //
    deleteRow(index, rows) {
      
      console.log(index,rows);
      
      rows.splice(index, 1);
    },
    dialogAdd($event){//
      this.dialogAddVisible = $event.close
      if($event.query) this.query()
    },
    handleEdit(index, row) {
      console.log(index, row);
    },
    // 
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {  //
      console.log(val);
      
      this.multipleSelection = val;
    },

    //
    handleSizeChange(val) {
      this.pageNation.pageSize = val;
      this.query()
      // console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      this.pageNation.pageNum = val;
      this.query()
      // console.log(`: ${val}`);
    },
    isExpire(expirydate){//
      if(expirydate && new Date(expirydate.substr(0,10)) < new Date()) return true
      else return false
    }
  },
  created(){
    this.query()
  }
};
</script>
<style lang="scss" scoped>
.el-row {
  padding-bottom: 10px;
}
.right {
  float: right;
}
</style>